<div class="sort"><h2>Сотировка</h2><div class="sort-box"><button v-for="(btn, index) in btns" :key="btn.name" :class="{active: isActive == index}" @click="sortRepos(index, btn.sortType)">{{btn.name}}</button></div></div><div class="repos"><div class="repos-item" v-for="repos in getReposSort" :key="repos.id"><div class="repos-item-left"><h3>{{repos.name}}</h3><p>Кол-во звёзд: {{repos.stargazers_count}}</p><p>Дата добавления: {{new Date(repos.created_at).toLocaleDateString()}}</p></div><a :href="repos.html_url">ПОСЕТИТЬ</a></div></div>